form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    padding: 30px;
    background-color: rgba(120, 131, 187, 0.5);
    box-shadow: 0px 0px 8px 0px #28272c47;
}

input {
    color: rgba(42, 43, 52, 0.8);
    margin-top: 20px;
    font-size: 20px;
    font-weight: 700;
    padding: 10px;
    border: none;
    border-radius: 5px;
    box-shadow: inset 0px 0px 8px 0px #33333347;
}

input::placeholder {
    font-weight: 400;
}

form>button {
    font-size: 20px;
    font-weight: 800;
    color: rgba(37, 38, 49, 0.8);
    margin-top: 30px;
    padding: 15px;
    border: none;
    box-shadow: 0 0 2px rgb(44, 42, 47);
    cursor: pointer;
    background-color: rgba(116, 126, 174, 0.742);
    border-radius: 5px;
    transition-duration: 400ms;
}

form>button:hover {
    transform: scale(1.1);
    background-color: rgba(79, 183, 93, 0.2);
}